import './assets/css/style.css';

const inputCep = document.querySelector('[data-js="input-cep"]');
const submitBtn = document.querySelector('[data-js="submit"]');

function cleanCep(cep) {
    const regex = /\d+/g;
    let int = cep.value.match(regex).join('');
    return int.slice(0,8)
};

async function getCep(cep) {
    try {
        if(cep.length < 8) {
            cep = 11010101;
        }
        const data = await fetch(`https://viacep.com.br/ws/${cep}/json/`)
        return data.json()
    } catch(e) {
        console.log('ERRO!!!!!', e);
        alert( 'erro de conexão, tente novamente!')
    }
};

function createData(data) {
    if(Object.keys(data).length < 2) {
        data = {
            logradouro: 'Cep inválido',
            bairro: 'inválido',
            localidade: 'inválido',
            uf: 'inválido',
            cep: 'inválido',
        }
    }
    document.querySelector('[data-js="logradouro"]').textContent = data.logradouro;
    document.querySelector('[data-js="bairro"]').textContent = data.bairro;
    document.querySelector('[data-js="cidade"]').textContent = data.localidade;
    document.querySelector('[data-js="estado"]').textContent = data.uf;
    document.querySelector('[data-js="cep"]').textContent = data.cep;
    console.table(data)
}

submitBtn.addEventListener('click', e => {
    e.preventDefault();
    if(inputCep.value == '') {
        console.log('valor inválido');
        return
    }
    const x = getCep(cleanCep(inputCep));
    x.then((e) => {
        createData(e)
        console.log(e);
    });
    inputCep.value = null

});

console.log(inputCep);